<template>
  <v-card
    height="100%"
  >
    <div
      id="gestion-docentes-dedicacion-tipo-nivel"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Tipo de dedicación por nivel / categoría.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('gestion-docentes-dedicacion-tipo-nivel')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as docentes.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="BarChart"
          />
        </v-responsive>
        <v-responsive style="width: 100%;">
          <v-simple-table
            dense
            class="cellheading mt-2"
          >
            <thead>
              <tr class="caption">
                <th rowspan="2">
                  Nivel / Categoría
                </th>
                <th colspan="2">
                  IDs (exclusiva y semi-exclusiva)
                </th>
                <th colspan="2">
                  Dedicación simple
                </th>
              </tr>
              <tr>
                <th>Valor Absoluto</th>
                <th>Procentaje</th>
                <th>Valor Absoluto</th>
                <th>Procentaje</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Profesores (A+B+C+Prof. Titular+Prof. Asociado+Prof. Adjunto)</td>
                <td>{{ dedicacionTipoNivelData.prof.ids.abs }}</td>
                <td>{{ dedicacionTipoNivelData.prof.ids.per }}%</td>
                <td>{{ dedicacionTipoNivelData.prof.sim.abs }}</td>
                <td>{{ dedicacionTipoNivelData.prof.sim.per }}%</td>
              </tr>
              <tr>
                <td>D1+JTP</td>
                <td>{{ dedicacionTipoNivelData.jtp.ids.abs }}</td>
                <td>{{ dedicacionTipoNivelData.jtp.ids.per }}%</td>
                <td>{{ dedicacionTipoNivelData.jtp.sim.abs }}</td>
                <td>{{ dedicacionTipoNivelData.jtp.sim.per }}%</td>
              </tr>
              <tr>
                <td>D2+Ayudante de 1ra</td>
                <td>{{ dedicacionTipoNivelData.ayu.ids.abs }}</td>
                <td>{{ dedicacionTipoNivelData.ayu.ids.per }}%</td>
                <td>{{ dedicacionTipoNivelData.ayu.sim.abs }}</td>
                <td>{{ dedicacionTipoNivelData.ayu.sim.per }}%</td>
              </tr>
            </tbody>
          </v-simple-table>
        </v-responsive>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { addPorcentajes } from '@/utils.js'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['docentes', 'mobileSize']),
    barChartData () {
      return {
        cols: [
          { id: 'Tipo de dedicación por nivel', label: 'Tipo de dedicación por nivel', type: 'string' },
          { type: 'string', role: 'tooltip', p: { html: true } },
          { id: 'IDs (exclusiva y semi-exclusiva)', label: 'IDs (exclusiva y semi-exclusiva)', type: 'number' },
          { role: 'annotation' },
          { id: 'Simple', label: 'Simple', type: 'number' },
          { role: 'annotation' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: 'Profesores (A+B+C+Prof. Titular+Prof. Asociado+Prof. Adjunto)' },
              { v: 0, f: this.createTooltip('Profesores (A+B+C+Prof. Titular+Prof. Asociado+Prof. Adjunto)', this.dedicacionTipoNivelData.prof) },
              {
                v: this.dedicacionTipoNivelData.prof.ids.per,
                f: this.dedicacionTipoNivelData.prof.ids.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoNivelData.prof.ids.per)}` },
              {
                v: this.dedicacionTipoNivelData.prof.sim.per,
                f: this.dedicacionTipoNivelData.prof.sim.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoNivelData.prof.sim.per)}` }
            ]
          },
          {
            c: [
              { v: 0, f: 'D1+JTP' },
              { v: 0, f: this.createTooltip('D1+JTP', this.dedicacionTipoNivelData.jtp) },
              {
                v: this.dedicacionTipoNivelData.jtp.ids.per,
                f: this.dedicacionTipoNivelData.jtp.ids.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoNivelData.jtp.ids.per)}` },
              {
                v: this.dedicacionTipoNivelData.jtp.sim.per,
                f: this.dedicacionTipoNivelData.jtp.sim.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoNivelData.jtp.sim.per)}` }
            ]
          },
          {
            c: [
              { v: 0, f: 'D2+Ayudante de 1ra' },
              { v: 0, f: this.createTooltip('D2+Ayudante de 1ra', this.dedicacionTipoNivelData.ayu) },
              {
                v: this.dedicacionTipoNivelData.ayu.ids.per,
                f: this.dedicacionTipoNivelData.ayu.ids.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoNivelData.ayu.ids.per)}` },
              {
                v: this.dedicacionTipoNivelData.ayu.sim.per,
                f: this.dedicacionTipoNivelData.ayu.sim.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoNivelData.ayu.sim.per)}` }
            ]
          }
        ]
      }
    },
    barChartOptions () {
      return {
        chartArea: {
          left: 200,
          right: 40,
          top: 40,
          bottom: 40
        },
        colors: this.colors,
        // colors: state.colores.investigadoresSexo,
        focusTarget: 'category',
        tooltip: {
          isHtml: true
          // textStyle: { color: 'white', bold: false }
        },
        // title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        isStacked: 'percent',
        legend: {
          position: 'top',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        bar: { groupWidth: '50%' },
        annotations: {
          // style: 'line'
          highContrast: false,
          textStyle: {
            color: 'white'
          }
        },
        hAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          },
          slantedText: false
        },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    dedicacionTipoNivelData () {
      // PROFESORES
      //     "Niv A gr 13 cat 26",
      //     "Niv B gr 10 cat 20",
      //     "Niv B gr 10 cat 22",
      //     "Niv B gr 12 cat 24",
      //     "Niv B gr 12 cat 26",
      //     "Niv C gr 07 cat 14",
      //     "Niv C gr 07 cat 15",
      //     "Niv C gr 08 cat 16",
      //     "Niv C gr 08 cat 17",
      //     "Niv C gr 09 cat 18",
      //     "Profesor Asociado",
      //     "ASOCIADO SIMPLE",
      //     "Profesor Titular",
      //     "Profesor Adjunto",
      //     "ADJUNTO SIMPLE",

      // D1+JTP
      //     "Niv D1 gr 04 cat 08",
      //     "Niv D1 gr 04 cat 07",
      //     "Niv D1 gr 05 cat 09",
      //     "Jefe Trab. Practicos",
      //     "Jef Trab Pract NM",
      //     "JTP SIMPLE",

      // D2+ Ayudante de 1ra
      //     "Niv D2 gr 02 cat 03",
      //     "Niv D2 gr 03 cat 05",
      //     "Ayudante de 1ra.",
      //     "AYUDANTE DE 1º SIMPL",

      // otros
      //     "Cat 6 CCT 366/06",
      //     "Cat 5 CCT 366/06",
      //     "Cat 3 CCT 366/06",
      //     "Cat 2 CCT 366/06",
      //     "Cat 4 CCT 366/06",
      //     "Cat 1 CCT 366/06",
      //     "Cat 7 CCT 366/06",
      //     "Director Col Secund",
      //     "Vicedirector",
      //     "Regente",
      //     "Prof nivel medio",
      //     "Bibliotecario",
      //     "Secretario Universid",
      //     "Decano de Instituto",
      //     "Secretario Instituto",
      //     "PROF EQUIPO ORIENTAC",
      //     "Prof. Eq. Orientacio",
      //     "Jefe/Coord/Dir dept",
      //     "Rector",
      //     "Jefe Gral Taller EP",
      //     "Jef Gral Ens Pract",
      //     "Prest. Serv. Sec LyT",
      //     "Prest Serv Com Mem",
      //     "Prest. Serv. Genero",
      //     "Prest Serv Dep",
      //     "Prest. Serv. Deporte",
      //     "Contr.Pres.Serv ASJU",
      //     "Prest. Serv. Dep",
      //     "Cont. Proy. Esp.",
      //     "Prest. Serv. Multi",
      //     "AYUDANTE 2º SIMPLE",
      //     "Prest de Serv Medios",
      //     "Maestros Ens Practic",
      //     "Prest. Serv. Cult",
      //     "Contr Prest Serv Med",
      //     "CHOFER",
      //     "MAF PS E",
      //     "MAF PS C",
      //     "MAF PS D"
      const docentesNivel = this.docentes.reduce((acc, cur) => {
        const nivA = new RegExp('Niv A', 'gm')
        const nivB = new RegExp('Niv B', 'gm')
        const nivC = new RegExp('Niv C', 'gm')
        const nivD1 = new RegExp('Niv D1', 'gm')
        const nivD2 = new RegExp('Niv D2', 'gm')
        const profesores = nivA.test(cur.descripcion_categoria) ||
          nivB.test(cur.descripcion_categoria) ||
          nivC.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Profesor Asociado' ||
          cur.descripcion_categoria === 'ASOCIADO SIMPLE' ||
          cur.descripcion_categoria === 'Profesor Titular' ||
          cur.descripcion_categoria === 'Profesor Adjunto' ||
          cur.descripcion_categoria === 'ADJUNTO SIMPLE'
        const d1jtp = nivD1.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Jefe Trab. Practicos' ||
          cur.descripcion_categoria === 'Jef Trab Pract NM' ||
          cur.descripcion_categoria === 'JTP SIMPLE'
        const d2ayu = nivD2.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Ayudante de 1ra.' ||
          cur.descripcion_categoria === 'AYUDANTE DE 1º SIMPL'
        const excsem = cur.descripcion_dedicacion === 'Exclusiva' ||
          cur.descripcion_dedicacion === 'Semi-Exclusiva' ||
          cur.descripcion_dedicacion === 'Semi - Excl Conven'
        const simple = cur.descripcion_dedicacion === 'Simple'
        return profesores
          ? { ...acc, prof: { ids: acc.prof.ids + (excsem ? 1 : 0), sim: acc.prof.sim + (simple ? 1 : 0) } }
          : d1jtp
            ? { ...acc, jtp: { ids: acc.jtp.ids + (excsem ? 1 : 0), sim: acc.jtp.sim + (simple ? 1 : 0) } }
            : d2ayu
              ? { ...acc, ayu: { ids: acc.ayu.ids + (excsem ? 1 : 0), sim: acc.ayu.sim + (simple ? 1 : 0) } }
              : acc
      },
      {
        prof: {
          ids: 0,
          sim: 0
        },
        jtp: {
          ids: 0,
          sim: 0
        },
        ayu: {
          ids: 0,
          sim: 0
        }
      })
      return this.addPorcentajes(docentesNivel)
    }
  },
  methods: {
    downloadIndicador (indicador) {
      this.$store.dispatch('createPng', indicador)
    },
    addPorcentajes (v) {
      return {
        prof: addPorcentajes(v.prof, v.prof.ids + v.prof.sim),
        jtp: addPorcentajes(v.jtp, v.jtp.ids + v.jtp.sim),
        ayu: addPorcentajes(v.ayu, v.ayu.ids + v.ayu.sim)
      }
    },
    createTooltip (title, value) {
      return `<div style="min-width: 370px; padding: 10px 10px 10px 10px; font-size: 13px; color: black;">
      <h3>${title}</h3>
      <ul>
      <li>IDs (exculsiva y semi-exclusiva): ${value.ids.per}% (${value.ids.abs})</li>
      <li>Simple: ${value.sim.per}% (${value.sim.abs})</li>
      </li>
      </ul></div>`
    },
    hiddeAnnotation (v) {
      return v <= 7 ? '' : `${v}%`
    }
  }
}
</script>
